<template>
    <div>
        <v-row>
            <v-btn color="primary" @click="onFL014" :loading="is_loading">
                FL014
            </v-btn>
        </v-row>
        <v-row>
            <v-col>
                <v-data-table :headers="headers"
                              :items="items"
                              :loading="is_loading"
                              dense
                              >
                </v-data-table>
            </v-col>
        </v-row>
    </div>
</template>

<script>
    export default {
        props: {
            engagementCd: String,
        },
        data() {
            return {
                isAuthorized: this.$store.state.isAuthorized,
                is_loading: false,
                items: [],
            };
        },
        computed: {
            headers() {
                return [
                    { text: '時間', value: 'log_dttm', width: '200px' },
                    { text: 'メッセージ', value: 'log_message' },
                ];
            },
        },
        methods: {
            async onFL014() {
                try {
                    this.is_loading = true;
                    const data = {
                        params: {
                            engagementCd: this.engagementCd,
                        }
                    };

                    const result = await this.gasPost('FL014', data);
                    if(result.data.status == 'OK'){
                        this.items = result.data.log;
                    }
                } finally {
                    this.is_loading = false;
                }

            }
        },
    };
</script>

